@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');


*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family:'Montserrat', sans-serif;
  background-color:#1a1a1c ;
  color: #fff;
}

.coin-app{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color:#fff;
}
.coin-search{
  margin-bottom: 64px;
  justify-content: center;
  display: flex;
  flex-direction: column;
  align-content: center;
  color: #fff;
}
.coin-text{
  margin-bottom: 32px;
  text-align: center;
  font-style: italic;
  font-family: 'Courier New', Courier, monospace;
  color: solid white;
  border: lightblue;
  box-shadow: white;
}
.coin-input{
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 4px;
  background-image: linear-gradient(-255deg,#fff  0%,#6A2C70 48%,#522546 100%);
  color:#3282B8 ;
}
.coin-input::placeholder{
  color:#3E2C41 ;
  size: 10px;
}
